import { Component, Injectable } from '@angular/core';
import * as d3 from 'd3';
import { HttpClient } from '@angular/common/http';
import { SvgDefs } from './svg-pattern.service';
import { __values } from 'tslib';
import { Canvas } from './cansvas-class.service';
import { GeneratedFile } from '@angular/compiler';
import { interval } from 'rxjs';
import { compileComponentFromRender2 } from '@angular/compiler/src/render3/view/compiler';


interface Node {
  name: string;
  type: string;
  services?: [{ name: string, owner: string }];
  fixated: boolean;
}

interface Link {
  source: string;
  target: string;
  messages: {
    Requests: any[];
    Responses: any[];
  };
}
export interface Graph {
  nodes: any[];
  links: any[];
  messages: {
    Requests: any[];
    Responses: any[];
    currentTimestamp: number;
  };
}
@Injectable({
  providedIn: 'any',
})
export class D3SvgService {
  public data: any;
  public graph: any;
  constructor(public height: number, public width: number, private http: HttpClient) {
  }
  /**
   * @param Links objekty přebírané z Links.json
   * @returns array spojení podle popisu z Network.json
   */
  private AssignLinks(Links): any {
    const links: Link[] = new Array<Link>();
    Links.Links.forEach(link => {
      links.push({ source: link.src, target: link.dest, messages: { Requests: [], Responses: [] } });
    });
    return links;
  }

  private AssignMessages(response): any {
    const Requests: any = new Array();
    const Responses: any = new Array();
    response.messages.forEach(message => (message.Type === 'REQUEST') ? Requests.push(message) : Responses.push(message));
    Requests.sort((a, b) => a.timestamp - b.timestamp);
    Responses.sort((a, b) => a.timestamp - b.timestamp);
    return { Requests, Responses };
  }
  /**
   *
   * @param Nodes objekty přebírané z Network.json
   * @returns array uzlů podle popisu z Network.json
   */
  private AssignNodes(Nodes): any {
    const nodes: Node[] = new Array<Node>();
    Nodes.network.forEach(node => {
      nodes.push({ name: node.id, type: node.Type, services: node?.services, fixated: false });
    });
    return nodes;
  }
  /**
   * @brief kontrola načtení dat z API do proměnné graph
   * @param graph údaje o grafu(Links, Nodes)
   * @returns graph
   */
  private GraphPms(graph: Graph): Promise<Graph> {
    return new Promise((resolve, reject) => {
      const interval = setInterval(() => {
        if (graph && graph.links && graph.nodes) {
          console.log('Graph: success', graph);
          resolve(graph);
          clearInterval(interval);
        } else {
          console.log('failed');
        }
      }, 100);
    });
  }
  /**
   * @brief kontrola přijmu dat z API
   * @param response subscribe pro Links.json nebo Network.json
   * @returns promise response
   */
  private GetDataPms(response: any): Promise<any> {
    return new Promise((resolve, reject) => {
      const interval = setInterval(() => {
        if (response) {
          resolve(response);
          clearInterval(interval);
        } else {
          console.log('Data fetch failed');
        }
      }, 500);
    });
  }

  /**
   * @brief přebírá data z API a přepisuje do graph
   */
  public AssignInputAndForce(data): void{
    const graph: Graph = {} as Graph;
    graph.links = this.AssignLinks(data.links);
    graph.messages = this.AssignMessages(data.messages);
    graph.nodes = this.AssignNodes(data.network);
    this.GraphPms(graph).then(res =>
      this.Force(res)
      );
  }
  public AssignDataAndForce(): void {
    const graph: Graph = {} as Graph;
    const urls = [
      { id: 0, path: './assets/Links.json' },
      { id: 1, path: './assets/Network.json' },
      { id: 2, path: './assets/messages.json' }
    ];
    urls.forEach(url => {
      this.http.get(url.path).subscribe(async (response) => {
        await this.GetDataPms(response);
        switch (url.id) {
          case 0:
            graph.links = this.AssignLinks(response);
            break;

          case 1:
            graph.nodes = this.AssignNodes(response);
            break;

          case 2:
            graph.messages = this.AssignMessages(response);
            this.Force(await this.GraphPms(graph));
            break;
        }
      });
    });
  }
  public destroy(){
    (document.querySelector('#simstop') as HTMLButtonElement).click();
    d3.select('svg').selectAll('*').remove();
  }
  /**
   * @brief vytváří force layout pro vizualizaci sítě
   * @param graph soubor Nodů a linek
   */
  private simState: 'running' | 'paused' | 'stopped'= 'stopped';
  private Force(graph: Graph): any {
    let cvs = null;
    const Sim: any = document.querySelector('#simstart');
    const pause: any = document.querySelector('#simplaypause');
    const stop: any = document.querySelector('#simstop');
    const balance: any = document.getElementById('bal') as HTMLInputElement;
    balance.addEventListener('change', change);
    Sim.addEventListener('click', () => {
      this.simState = 'running';
      cvs = new Canvas(graph);
      stop.removeAttribute('hidden');
      pause.removeAttribute('hidden');
      Sim.src = './../../assets/Images/restart.svg';
    });
    pause.addEventListener('click', () => {
      if (this.simState === 'running'){
      cvs.pauseSimulation();
      this.simState = 'paused';
      pause.src = './../../assets/Images/pause.svg';
    }else{
      cvs.resumeSimulation();
      this.simState = 'running';
      pause.src = './../../assets/Images/play.svg';
    }
    });
    stop.addEventListener('click', () => {
      this.simState = 'stopped';
      cvs.stopSimulation();
      stop.setAttribute('hidden', true);
      pause.setAttribute('hidden', true);
      Sim.src = './../../assets/Images/play.svg';
    });
    const svg = d3.select('svg');
    const r = 40;
    const tooltip = new tooltipClass(graph, r);
    const simulation = d3
      .forceSimulation(graph.nodes)
      .force(
        'link',
        d3
          .forceLink()
          .id((d) => d.name)
          .links(graph.links)
      )
      .force('collide', d3.forceCollide(r * 1.5))
      .force('charge', d3.forceManyBody().strength(-40))
      .on('tick', ticked);

    SvgDefs(svg);

    const link = svg
      .append('g')
      .attr('class', 'links')
      .selectAll('line')
      .data(graph.links)
      .enter()
      .append('line')
      .attr('stroke-width', (d) => 1)
      .attr('stroke', (d) => 'black');
    const linkOverlay = svg
      .append('g')
      .attr('class', 'links')
      .selectAll('line')
      .data(graph.links)
      .enter()
      .append('line')
      .attr('stroke-width', (d) => 30)
      .attr('stroke', (d) => 'transparent')
      .style('position', 'absolute')
      .style('z-index', '20')
      .on('mouseenter', (e, d) => { if (!tooltip.isTooltipHidden && !tooltip.On) { tooltip.MsgTooltipHover(e, d); } })
      .on('mouseleave', () => tooltip.TooltipHidden())
      .on('click', () => tooltip.On = !tooltip.On);
    const node = svg
      .append('g')
      .attr('class', 'nodes')
      .selectAll('circle')
      .data(graph.nodes)
      .enter()
      .append('circle')
      .attr('r', r)
      .attr('fill', (d) => `url(#${d.type})`)
      .on('dblclick', dblclick)
      .on('mouseenter', (e, d) => { if (!tooltip.isTooltipHidden) { tooltip.TooltipHover(e, d); } })
      .on('mouseleave', () => tooltip.TooltipHidden())
      .call(
        d3
          .drag()
          .on('start', dragstarted)
          .on('drag', dragged)
          .on('end', dragended),
      );

    const label = svg.selectAll(null)
      .data(graph.nodes)
      .enter()
      .append('text')
      .text((d) => d.name)
      .style('text-anchor', 'middle')
      .style('fill', '#555')
      .style('font-family', 'Arial')
      .style('font-size', 12);


    /**
     * @brief vytvoří okénko s informaci o jednotlivých nodech
     * @param node uzel, pro který se má tooltip objevit
     */




    /**
     * @brief zobrazí tooltip
     * @param event vyvolaná interakcí uživatele
     * @param node uzel, pro který je událost vyvolaná
     * @returns styly pro tooltip (průhlednost, z-index a pozici xy)
     */
    /**
     * @brief schová tooltip
     * @returns styly pro tooltip (průhlednost 100%, přenese do pozadí)
     */


    function change(): void {
      if (!balance.checked) {
        graph.nodes.forEach(n => {
          d3.select(this).classed('fixed', n.fixed = true);
          n.fx = n.x;
          n.fy = n.y;
        });
      } else {
        graph.nodes.forEach(n => {
          if (!n.fixated) {
            d3.select(this).classed('fixed', n.fixed = false);
            n.fx = null;
            n.fy = null;
          }
        });
      }
    }

    /**
     * @brief ticky grafu
     */
    function ticked(): void {
      node
        .attr('cx', (d) => d.x = Math.max(r, Math.min(innerWidth - r, d.x)))
        .attr('cy', (d) => d.y = Math.max(r, Math.min(innerHeight - r, d.y)));
      link
        .attr('x1', (d) => d.source.x)
        .attr('y1', (d) => d.source.y)
        .attr('x2', (d) => d.target.x)
        .attr('y2', (d) => d.target.y);
      linkOverlay
        .attr('x1', (d) => d.source.x)
        .attr('y1', (d) => d.source.y)
        .attr('x2', (d) => d.target.x)
        .attr('y2', (d) => d.target.y);
      label.attr('x', (d) => d.x)
        .attr('y', (d) => d.y - (r + 10));
    }
    /**
     * @brief příprava na drag
     * @param event event vyvolaný uživatelem na svg objektu
     * @param d uzel na kterém byl event vyvolaný
     */
    function dragstarted(event, d): void {
      if (!event.active) { simulation.alphaTarget(0.3).restart(); }
      d3.select(this).classed('fixed', d.fixed = true);
      tooltip.TooltipHidden();
      d.fixated = true;
      tooltip.isTooltipHidden = true;
      tooltip.On = false;
    }
    /**
     * @brief mění pozici nodů v závislosti na pohybu a pozici myši
     */
    function dragged(event, d): void {
      d.fx = event.x;
      d.fy = event.y;
    }
    /**
     * @brief konec tažení
     */
    function dragended(event, d): void {
      if (!event.active) { simulation.alphaTarget(0); }
      tooltip.isTooltipHidden = false;
    }
    /**
     * @brief uvolňuje připnutý node
     */
    function dblclick(event, d): void {
      if (!balance.checked) { return; }
      d.fixated = false;
      d3.select(this).classed('fixed', d.fixed = false);
      d.fx = null;
      d.fy = null;
    }
  }

}

export class tooltipClass {
  private messageKeys=[
    'ID',
    'Type',
    'Origin',
    'Source',
    'Target',
    'Destination service',
    'Source service',
    'Action',
    'Session',
    'Authorization',
    'timestamp',

  ]
  private simulation;
  private tooltip;
  private RRs = null;
  private RRsCheck = false;
  public isTooltipHidden = false;
  public On = false;
  private linkref = null;
  constructor(private graph, public r) {
    this.tooltip = d3.select('body')
      .append('div')
      .attr('class', 'tooltip')
      .style('position', 'absolute')
      .style('padding', '10px')
      .style('z-index', '10')
      .style('min-width', '150px')
      .style('min-height', '50px')
      .style('background-color', 'rgba(32, 32, 60, 0.8)')
      .style('color', 'silver')
      .style('border-radius', '5px')
      .style('opacity', '0')

      .style('transition:', 'opacity 0.3s linear')
      .text('')
      .on('dblclick', () => {
        this.On = false;
        return this.TooltipHidden();
      });
  }
  public TooltipHidden(): void {
    if (!this.On) {
      this.linkref = null;
      this.tooltip.style('opacity', '0').style('z-index', '-10');
    }
  }
  public loadTooltipContent(node: any): void {
    let htmlContent = '<div>';
    htmlContent += '<span class="name"> Node: ' + node.name + '<\/span><br>';
    htmlContent += '<span class="service">Services:<\/span><hr>';
    node.services?.forEach(service => {
      Object.keys(service).forEach(key => htmlContent += '<b>' + key + ': <\/b>' + service[key] + ' ');
      htmlContent += '<hr>';
    });
    htmlContent += '<\/div>';
    this.tooltip.html(htmlContent);
  }

  public MsgTooltipHover(event, link): any {
    if (!this.isTooltipHidden && !this.On) {
      this.linkref = link;
      this.loadMsgTooltip(link);
      this.tooltip
        .style('top', event.clientY + 'px')
        .style('left', event.clientX + 10 + 'px')
        .style('opacity', '100')
        .style('z-index', '10');
    }
  }
  public loadMsgTooltip(link: Link): void {
    let htmlContent = `<label class="switch"><input id="RRS" type="checkbox" ${this.RRsCheck ? 'checked' : ''} ><span class="RRs"><\/span><\/label><br>`;
    htmlContent += `<span class="service" id="${(link.source as any).name + (link.target as any).name}">Intercepted communication: <\/span><hr>`;
    const Type = this.RRsCheck ? 'Responses' : 'Requests';
    if (link.messages[Type].length === 0) {
      link.messages[Type] = this.graph.messages[Type].filter(item =>
        (Object.values(link.source).includes(item.Source) || (Object.values(link.target).includes(item.Source)))
        &&
        (Object.values(link.source).includes(item.target) || (Object.values(link.target).includes(item.Source)))
      );
    }
    const activemessages = link.messages[Type].filter(message =>
      message.timestamp <= this.graph.messages.currentTimestamp &&
      message.timestamp >= this.graph.messages.currentTimestamp - 5
    );

    this.messageKeys
    .forEach(key => {
      let active = activemessages[activemessages.length - 1];
      if (active){
        active = active[key];
      }else{
        active = '';
      }
      htmlContent += `<b>${key}: <\/b> <span id=${key.replace(' ', '_')}>${active}<\/span><br>`;
    });
    htmlContent += '<hr>';
    htmlContent += '<hr>';
    htmlContent += '<\/div>';
    this.tooltip.html(htmlContent);
    this.RRs = document.getElementById('RRS') as HTMLInputElement;
    this.RRs.checked = this.RRsCheck;
    this.RRs.addEventListener('change', () => {
      this.RRsCheck = this.RRsCheck ? false : true;
      this.loadMsgTooltip(link);
    });
  }
  public TooltipHover(event, node): any {
    if (!this.isTooltipHidden && !this.On) {
      this.loadTooltipContent(node);
    }

    this.tooltip.style('top', (node.y - 10) + 'px').style('left', (node.x + this.r + 10) + 'px').style('opacity', '100').style('z-index', '10');
  }


  public runSimulation() {
    this.simulation = 'run';
    console.log(this.simulation);
  }
  public stopSimulation() {
    this.simulation = 'stop';
    console.log(this.simulation);
  }
  public pauseSimulation(){
    this.simulation = 'paused';
    console.log(this.simulation);
  }
}
