import { preserveWhitespacesDefault } from '@angular/compiler';
import { Component, Input, OnInit } from '@angular/core';
import { Canvas } from '../cansvas-class.service';
import { LevelNodes, getMaxYLevel, getMaxXlevel } from '../levels.service';
import * as NodesData from '../../assets/Network.json';
import { D3SvgService } from '../d3-svg.service';
import { HttpClient } from '@angular/common/http';


export interface Node {
  ID: string;
  type: string;
  connections: string[];
  Ylevel: number;
  Xlevel: number;
  CardinalityFwd: number;
}
interface input {
  links: object | undefined;
  messages: object | undefined;
  network: object | undefined;
}

@Component({
  selector: 'app-network',
  templateUrl: './network.component.html',
  styleUrls: ['./network.component.less']
})
export class NetworkComponent implements OnInit {
  public prov1 = false;
  public prov2 = false;
  public prov3 = false;
  public Nodes: Node[] = new Array<Node>();
  private Dataset: input = new Input();
  constructor(private http: HttpClient) { }
  public IsPopped = false;
  public d3: D3SvgService = new D3SvgService(innerHeight, innerWidth, this.http);
  ngOnInit(): void {
    NodesData.network.forEach(node =>
      this.Nodes.push({ ID: node.id, connections: node.connections, type: node.Type, Ylevel: 0, Xlevel: 0, CardinalityFwd: 0 }));
    this.Nodes = LevelNodes(this.Nodes);
    const Ylevels = getMaxYLevel(this.Nodes);
    const Xlevels = getMaxXlevel(this.Nodes);
    this.d3.AssignDataAndForce();
  }
  public simulateFromInput(){
    this.checkInput();
    if (this.checkInput() === true){
      this.d3.destroy();
    this.d3 = new D3SvgService(innerHeight, innerWidth, this.http);
     this.d3.AssignInputAndForce(this.Dataset);
    }
  }
  public checkInput(): string | boolean{
    if (this.Dataset.network){
      this.prov1 = true;
    }
    if (this.Dataset.messages){
      this.prov3 = true;
    }
    if (this.Dataset.links){
      this.prov2 = true;
    }
    if(!this.Dataset.links || !this.Dataset.messages || !this.Dataset.network ){
      return false;
    }
    return true;
  }
  public onFileSelected(e: any): void {
    this.prov1 = false;
    this.prov2 = false;
    this.prov3 = false;
    const inputNode: any = document.querySelector('#file');
    this.Dataset = new Input();
    if (typeof (FileReader) !== 'undefined') {
      for (const file of e.target.files) {
        const reader = new FileReader();
        reader.readAsText(file);
        reader.onload = (event: any) => {
          const data = JSON.parse(event.target.result);
          const name = file.name.toLowerCase();
          if (name.match(`^(?!.*(nodes|messages|network)).*(links).*`)) {
            this.Dataset.links = data;
            console.log('data assigned - links');
            return;
          }
          if (name.match(`^(?!.*(nodes|links|network)).*(messages).*`)) {
            this.Dataset.messages = data;
            console.log('data assigned - messages');
            return;
          }
          if (name.match(`^(?!.*(messages|links)).*(nodes|network).*`)) {
            this.Dataset.network = data;
            console.log('data assigned - network');
            return;
          }
        };
      }
    }
  }
  public CardUp() {
    const card = document.getElementById('card') as HTMLElement;
    const chevron = document.getElementById('chevron') as HTMLElement;
    if (!this.IsPopped) {
      card.classList.add('up');
      chevron.classList.add('up');
    } else {
      card.classList.remove('up');
      chevron.classList.remove('up');
    }
    this.IsPopped = !this.IsPopped;
  }
}

